.test-font-header {
    font-size: 45px;
    font-weight: 600;
}

.procedure-image {
    width: 75%;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

.procedure-image > div > img{
    width: 100%;
    height: auto;

    max-width: 150px;
}

.procedure-image > img {
    width: 20%;
    height: auto;
    
    max-width: 100px;
}

.procedure-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    min-height: 500px;
}

.procedure-image-box {
    padding-top: 64px;
}

.procedure-image-box > p{
    font-size: 18px;
    font-weight: 600;
    text-align: center;

    padding-top: 20px;
}