@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;

  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.uxfac-logo {
    width: 320px;
    height: auto;
}

.uxfac-logo-container {
    height: 200px;

    display: flex;
    justify-content: center;

    align-items: center;
}
.condition-component-container {
    width: 100%;
    height: 100%;
}

.condition-component-title {
    font-size: 22px;
    font-weight: 600;

    text-align: center;

    padding-bottom: 20px;
}

.condition-component-text {
    font-size: 18px;
    padding: 0px 10px 0px 10px;
    text-align: center;

}
.condition-font-header {
    font-size: 45px;
    font-weight: 600;

    margin-bottom: 30px;

    text-align: center;
}

.condition-font {
    font-size: 18px;
    color: #000000;
    opacity: 0.5;
    margin: 10px 0px 0px 0px;

    text-align: center;
}

.condition {
    margin-top: 120px;
    margin-bottom: 100px;
}
.test-font-header {
    font-size: 45px;
    font-weight: 600;
}

.procedure-image {
    width: 75%;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

.procedure-image > div > img{
    width: 100%;
    height: auto;

    max-width: 150px;
}

.procedure-image > img {
    width: 20%;
    height: auto;
    
    max-width: 100px;
}

.procedure-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    min-height: 500px;
}

.procedure-image-box {
    padding-top: 64px;
}

.procedure-image-box > p{
    font-size: 18px;
    font-weight: 600;
    text-align: center;

    padding-top: 20px;
}
.recruit-text {
    font-size: 18px;
    color: #000000;
    opacity: 0.5;
    margin: 10px 0px 0px 0px;

    padding-left: 20px;
}

.recruit-header {
    font-size: 32px;
    font-weight: 600;
    text-align: center;
    margin: 120px 0px 30px 0px;
}

.test-font-header {
    font-size: 45px;
    font-weight: 600;
    text-align: center;

    margin-top: 100px;
}

.how-to-can {
    font-size: 20px;
    opacity: 0.6;
    margin-top: 70px;
    margin-bottom: 100px;

    text-align: center;
}
.row-container {
    width: 100%;
    height: 100%;

    margin: 0px;
}
.footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 300px;
}

.footer-logo {
    width: 120px;
    height: auto;
}

.footer-text {
    margin-top: 20px;
    font-size: 13px;
}
.person-container {
    width: 100%;
    height: 100%;
    min-height: 600px;
    margin: 30px 0px 30px 0px;

    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.person-img {
    width: auto;
    max-width: 300px;
    max-height: 500px;

    border-radius: 20px;
}

.person-title {
    text-align: left;
    margin-bottom: 20px;
}
.person-title-intro {
    font-size: 32px;
    margin-right: 22px;
}
.person-title-name {
    font-size: 45px;
    font-weight: 600;
}


.person-img-container {
    width: 100%;
    height: auto;
    min-height: 300px;
    max-height: 500px;
    display:flex;
    justify-content: center;
    align-items: center;

    border-radius: 20px;

    /* background-color: #e8e8e8; */
}


.person-intro-box {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
}

.menu-bar {
    text-align: center;
    font-size: 18px;

    margin-bottom: 50px;
}

.menu-bar > a {
    color: #000;
}

.menu-bar > span {
    margin: 0px 15px 0px 15px;
}
.particles-uxfac {
    width: 100%;
    height: 100%;
    background-color: #000;
    position: absolute;
}

.particles-contents {
    font-family: "Roboto Slab", sans-serif;

    color: white;
    font-size: 5em;
    font-weight: 100;
    position: absolute;

    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
}

.particles-contents img {
    width: 300px;
    height: 100%;
}

.particles-contents p {
    margin: 1rem 0 1rem 0;
    font-size: 0.25em;
}

.svg {
    -webkit-filter: invert(1);
            filter: invert(1);
    opacity: 0.8;
}

.particles-contents a {
    color: white;
}

html|* > svg {
    transform-origin: 50% 50% 0px;
}
svg:not(:root),
symbol,
image,
marker,
pattern,
foreignObject {
    overflow: hidden;
}
* {
    transform-origin: 0px 0px 0px;
}
polyline[Style] {
    fill: none;
    stroke: rgb(0, 255, 0);
    stroke-width: 0.508;
    stroke-dasharray: 1.524, 1.016;
    stroke-opacity: 1;
}

