@import url("https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap");

.particles-uxfac {
    width: 100%;
    height: 100%;
    background-color: #000;
    position: absolute;
}

.particles-contents {
    font-family: "Roboto Slab", sans-serif;

    color: white;
    font-size: 5em;
    font-weight: 100;
    position: absolute;

    top: 50%;
    left: 50%;

    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}

.particles-contents img {
    width: 300px;
    height: 100%;
}

.particles-contents p {
    margin: 1rem 0 1rem 0;
    font-size: 0.25em;
}

.svg {
    filter: invert(1);
    opacity: 0.8;
}

.particles-contents a {
    color: white;
}

html|* > svg {
    transform-origin: 50% 50% 0px;
}
svg:not(:root),
symbol,
image,
marker,
pattern,
foreignObject {
    overflow: hidden;
}
* {
    transform-origin: 0px 0px 0px;
}
polyline[Style] {
    fill: none;
    stroke: rgb(0, 255, 0);
    stroke-width: 0.508;
    stroke-dasharray: 1.524, 1.016;
    stroke-opacity: 1;
}
