.person-container {
    width: 100%;
    height: 100%;
    min-height: 600px;
    margin: 30px 0px 30px 0px;

    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.person-img {
    width: auto;
    max-width: 300px;
    max-height: 500px;

    border-radius: 20px;
}

.person-title {
    text-align: left;
    margin-bottom: 20px;
}
.person-title-intro {
    font-size: 32px;
    margin-right: 22px;
}
.person-title-name {
    font-size: 45px;
    font-weight: 600;
}


.person-img-container {
    width: 100%;
    height: auto;
    min-height: 300px;
    max-height: 500px;
    display:flex;
    justify-content: center;
    align-items: center;

    border-radius: 20px;

    /* background-color: #e8e8e8; */
}


.person-intro-box {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
}
